* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin-top: 10px;
}
 
input, button {
  font-size: 16px;
  padding: 5px;
}

button {
  cursor:pointer;
  padding: 5px;
  border: 1px solid black;
}

#root {
  height:100vh;
}

a {
  color:#305090;
  text-decoration: none;
}

a:hover {
  color: orange;
}

.container {
  display:flex;
  flex-direction: column;
  min-height: 100vh;
}

.light {
  background-color: white;
  color:black;
  transition: 0.5s;
}

.dark {
  background-color: rgb(32,33,36);
  color:white;
  transition: 0.5s;
}

.dark button {
  background-color:grey ;
  cursor:pointer;
  padding: 5px;
  border:1px solid #c0c0c0;
}

.dark a {
  color:#40c0f0;
}
a:hover {
  color: orange;
}

.active {
  font-weight: bold;
}
.main {
  padding: 10px;
  flex: auto;
}

.header {
  padding:10px;
  min-height:50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px #404040 solid;
}

.footer {
  padding:10px;
  min-height:50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px #404040 solid;
}

.blog {
  display: flex;
  width:100%;
  flex-wrap: wrap;
}

.content {
  flex:1 75%;
  padding: 10px;
}

.sidebar {
  flex: 1 25%;
  padding: 10px;
}

.form {
  max-width: 800px;
}

.form-item {
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
}

.form-item > label {
 flex: 1 1 100px;
}

.form-item > input
.form-item > textarea
.form-item > span
.form-item > button {
  flex: 3 1 300px;
}

.error {
  color: red;
}